import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.vidyadaan.pages.HomePage() }
        ctx.router.register("/EventDetails/{year}/{index}") {
                org.example.vidyadaan.pages.EventDetails() }
        ctx.router.register("/contact_us") { org.example.vidyadaan.pages.Contact() }
        ctx.router.register("/events/{year}") { org.example.vidyadaan.pages.Events() }
        ctx.router.register("/team") { org.example.vidyadaan.pages.Team() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Contact_Us.WaveStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Contact_Us.SendButtonStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.sections.resultStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.sections.resultLinkStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.mainHeading)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.heading)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.headingValue)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.EventBasicDetailsContainer)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.EventBasicDetails1Container)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.heading1)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.headingvalue1)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.EventCompleteDetailsContainer)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.RegButtonStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.qrstyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.PosterStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.PosterStyle1)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.PosterStyle2)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.titlestyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.ticket1style)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.EventDetails.style.ticket1Underlinestyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.Eventcardstyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.Leftcardstyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.dateStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.EventCardStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.DateContainerStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.EventInfoStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Events.styles.ButtonStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.aboutUsContainer)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.BackToTopStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.TitleStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.TitleStyle1)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.TitleStyle2)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.dataStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.SocialLinkIconLink)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.ContactSectionContainer)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.MenuItemStyle2)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.HeaderBackground2)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.SubMenuItemStyle2)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.MenuItemStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.HeaderBackground)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.SubMenuItemStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.EventBoxStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.EventPosterStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.Updates_Container)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.Updates_Box)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.Updates_text_Style)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.WhatWeDoStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.GroupPicStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.WhatwedoTitle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Styles.WhatwedoDesc)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Team.styles.V_logoStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Team.styles.FadeIn)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Team.styles.TeamContainer)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Team.styles.TeamCardStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Team.styles.socialLinksStyle)
        ctx.theme.registerComponentStyle(org.example.vidyadaan.Team.styles.socialMediaIcon)
        ctx.stylesheet.registerKeyframes(org.example.vidyadaan.EventDetails.style.swing)
        ctx.stylesheet.registerKeyframes(org.example.vidyadaan.EventDetails.style.swing1)
        ctx.stylesheet.registerKeyframes(org.example.vidyadaan.Team.styles.FadeInAnimation)
        ctx.stylesheet.registerKeyframes(org.example.vidyadaan.Team.styles.spin)
        org.example.vidyadaan.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "vidyadaan")
        ) { org.example.vidyadaan.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
